.footer {
  width: 100%;
  height: 6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  box-sizing: border-box;
  background-color: var(--main-light-color);
  opacity: 0.8;
  background-size: 9px 9px;
  background-image: repeating-linear-gradient(
    45deg,
    #eceeda 0,
    #eceeda 0.9px,
    #3d85f9 0,
    #3d85f9 50%
  );
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}

.input {
  width: 70%;
  height: 100%;
  padding-left: 1rem;
  box-sizing: border-box;
  border-style: none;
  border-top-left-radius: 1rem;
  border-bottom-left-radius: 1rem;
  background-color: var(--input-background-color);
  /* background-color: #eceeda; */
  font-size: 1.25rem;
  font-family: 'NotoSansKR';
  color: var(--font-color);
}

.input:focus {
  outline: none;
}

.button {
  width: 30%;
  height: 100%;
  padding: 0;

  background-color: var(--main-light-color);
  border-style: solid;
  border-color: var(--input-background-color);
  border-width: 4px;
  color: var(--background-light-color);
  font-size: 1.5rem;
  font-family: 'NotoSansKR';
  text-transform: uppercase;
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;
}

.button:hover {
  background-color: var(--input-background-color);
  border-style: none;
  color: #3d85f9;
  cursor: pointer;
}

.button:active {
  background-color: #ff362d;
  color: #eceeda;
  border: solid;
  border-width: 4px;
}
