.main {
  width: 100%;
  height: 100%;
}
ul {
  width: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0.5rem 1.5rem;
}
li {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  list-style: none;

  padding: 8px 0 8px 0;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-color: var(--main-light-color);
}
.wrapTitle {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.checkbox {
  width: 1.25rem;
  height: 1.25rem;
  margin: 0;
  appearance: none;
  background-color: var(--background-light-color);
  padding-right: 8px;
  border-style: solid;
  border-width: 1px;
  border-radius: 0.25rem;
  border-color: var(--main-light-color);
}

.checkbox:hover {
  cursor: pointer;
  background-color: var(--main-light-color);
}

.checkbox:checked {
  background-color: var(--main-light-color);
  background-image: url('../../icons/check.svg');
  background-repeat: no-repeat;
  background-size: contain;
  /* filter: invert(43%) sepia(59%) saturate(2003%) hue-rotate(201deg)
    brightness(99%) contrast(97%); */
}

.title {
  font-size: 1.25rem;
  font-family: 'NotoSansKR';

  padding-left: 0.5rem;
  color: var(--font-color);
}

.delete {
  display: flex;
  width: 1.5rem;
  height: 1.5rem;
  padding: 0 2px;
  border: none;
  background-color: transparent;
}

.icon {
  width: 100%;
  height: 100%;
  filter: invert(48%) sepia(54%) saturate(3353%) hue-rotate(200deg)
    brightness(97%) contrast(102%);
}

.delete:hover {
  background-color: var(--background-light-color);
  outline-color: var(--point-color);
  outline-style: solid;
  border-radius: 0.5rem;
  outline-width: 1px;
  cursor: pointer;
}

.icon:hover {
  filter: invert(54%) sepia(92%) saturate(5400%) hue-rotate(340deg)
    brightness(98%) contrast(112%);
}
