.ul {
  display: flex;
  flex-direction: row;
  width: auto;
  padding: 0;
  border: none;
}

button {
  background: none;
  border: none;
  padding: 0;
  margin: 0;
}
.li {
  padding: 0;
  border: none;
}

.header {
  width: 100%;
  height: 6rem;

  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 0 1rem 0 1rem;
  box-sizing: border-box;

  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-color: var(--main-light-color);
  background-color: var(--background-light-color);
}

.theme {
  width: 1.6rem;
  height: 1.6rem;
  filter: invert(48%) sepia(54%) saturate(3353%) hue-rotate(200deg)
    brightness(97%) contrast(102%);
  margin-left: 0.5rem;
}

.theme:hover {
  transform: scale(1.2);
  transition: transform 0.3s;

  cursor: pointer;
}

.theme:active {
  filter: invert(54%) sepia(92%) saturate(5400%) hue-rotate(340deg)
    brightness(98%) contrast(112%);
}

.button {
  border-style: solid;
  border-width: 2px;
  border-radius: 0.5rem;
  padding: 0.25rem 0.75rem;
  margin: 6px;
  text-transform: uppercase;
  font-size: 1.5rem;
  font-family: 'NotoSans-SemiBold';
}

.button_light_theme {
  background-color: var(--background-light-color);
  border-color: var(--main-light-color);
  color: var(--main-light-color);
}

.button_dark_theme {
  background-color: var(--background-light-color);
  border-color: var(--main-light-color);
  color: var(--font-color);
}

.button:hover {
  background-color: var(--main-light-color);
  color: var(--background-light-color);
  cursor: pointer;
}

.button_active {
  background-color: var(--point-color);
  color: var(--background-light-color);
}
