.body {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 540px;
  height: 620px;
  left: calc(50% - 270px);
  top: calc(50% - 310px);
  background-color: var(--body-color);
  border-radius: 1rem;

  box-shadow: 1px 1px 22px 7px rgba(59, 59, 59, 0.75);
  -webkit-box-shadow: 1px 1px 22px 7px rgba(59, 59, 59, 0.75);
  -moz-box-shadow: 1px 1px 22px 7px rgba(59, 59, 59, 0.75);
}
