:root {
  --main-light-color: #3d85f9;
  --body-color: #f5f2f3;
  --background-light-color: #eceeda;
  --font-color: #3b3b3b;
  --point-color: #ff362d;
  --input-background-color: #f5f2f3;
}

.dark {
  --body-color: #302e2e;
  --background-light-color: #3a3a38;
  --font-color: #c7c5c5;
  --input-background-color: #353432;
}

body {
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  background-color: #d1d0d6;
}

@font-face {
  font-family: 'NotoSans-Regular';
  src: url('./fonts/NotoSans_Condensed-Regular.ttf');
}

@font-face {
  font-family: 'NotoSans-SemiBold';
  src: url('./fonts/NotoSans_Condensed-SemiBold.ttf');
}

@font-face {
  font-family: 'NotoSansKR';
  src: url('./fonts/NotoSansKR-Medium.ttf');
}
